/* eslint-disable react/no-deprecated */
import React, { useEffect, CSSProperties } from 'react';
import Styles from '@assets/scss/TableSheet.module.scss';
import { IBaseItem, MouseEventMap, SheetModifyRect } from '@dataTypes';
import config from '@sheet/config';
import StoreManager from '@sheet/helper/store-manager';
import { cLog } from '@debug/logger';
// import { difference, isDifferenceObject } from '@sheet/utils';

interface CellItemProps {
    data: IBaseItem;
    mouseEventMap?: MouseEventMap;
    useCellOver?: boolean;
    addStyle?: CSSProperties;
    style?: CSSProperties;
}

declare let window;

const TABLE_SHEET_SIZE = config.SHEET_TABLE.TABLE_SHEET_SIZE;
const { CELL_WIDTH, CELL_HEIGHT, SPAN_TEXT_PADDING } = TABLE_SHEET_SIZE;
const OVER_UPDATE_MARGIN = 5;

export default class CellItem extends React.Component<CellItemProps> {
    state = {
        renderFlag: false,
    };

    _doubletapTimer: any = null;
    _doubletapDeltaTime = 300;

    cellRef: React.RefObject<HTMLDivElement>;
    textRef: React.RefObject<HTMLSpanElement>;

    _focusOffset?: number;

    constructor(props) {
        super(props);
        this.cellRef = React.createRef();
        this.textRef = React.createRef();
    }

    componentWillUnmount() {}

    componentWillMount() {}

    componentDidMount() {
        cLog('[CellItem] componentDidMount');
        this._cellOverUpdate();
    }

    componentDidUpdate() {
        cLog('[CellItem] componentDidUpdate');
        this._cellOverUpdate();
    }

    shouldComponentUpdate(nextProps) {
        if (this.props && nextProps) {
            const { data, addStyle, style } = this.props;
            const { data: nextData, addStyle: nextAddStyle, style: nextStyle } = nextProps;

            // if (nextStyle && style) {
            //     console.log(difference(nextStyle, style));
            //     console.log(isDifferenceObject(nextStyle, style));
            // }

            // return false;
        }
        return true;
    }

    cellOverReset() {
        if (this.cellRef.current) {
            this.cellRef.current.style.zIndex = '';
        }

        if (this.textRef.current) {
            this.textRef.current.style.width = '';
            this.textRef.current.style.overflow = '';
            this.textRef.current.style.pointerEvents = 'none';
        }
    }

    _cellOverUpdate() {
        this.cellOverReset();
        const { data, useCellOver } = this.props;
        if (!useCellOver) return;
        if (this.textRef.current) {
            this.textRef.current.style.width = '';
            const { offsetWidth } = this.textRef.current;
            if (offsetWidth > CELL_WIDTH + OVER_UPDATE_MARGIN) {
                const textCellLen = Math.ceil(offsetWidth / CELL_WIDTH);
                const { col, row, str } = data;

                const { sheetItems } = StoreManager.state.sheet;
                if (col !== undefined && row !== undefined && sheetItems) {
                    const rows = sheetItems[col];
                    const len = row + textCellLen > rows.length ? rows.length : row + textCellLen;
                    let cnt = 0;
                    for (let i = row + 1; i < len; i++) {
                        if (rows[i].str) break;
                        cnt++;
                    }

                    if (this.cellRef.current && cnt) {
                        // this.cellRef.current.style.width = `${(cnt + 1) * CELL_WIDTH}px`;
                        this.cellRef.current.style.zIndex = '10';
                        let width = (cnt + 1) * (CELL_WIDTH - 1);
                        width = width > offsetWidth ? offsetWidth : width;
                        this.textRef.current.style.width = `${width}px`;
                        this.textRef.current.style.overflow = 'hidden';
                        this.textRef.current.style.pointerEvents = 'none';
                    }
                }
            }
        }
    }

    _onClick = (e) => {
        e.preventDefault();
        const { data, mouseEventMap } = this.props;
        const { click, dobuleClick } = mouseEventMap || {};

        if (this._doubletapTimer == null) {
            // this._textClickPosition(e)
            this._doubletapTimer = setTimeout(() => {
                this._doubletapTimer = null;
                if (click) click(data, e);
            }, this._doubletapDeltaTime);
        } else {
            clearTimeout(this._doubletapTimer);
            this._doubletapTimer = null;
            if (dobuleClick) dobuleClick(data, e);
        }
    };

    _textClickPosition = (e) => {
        const { data } = this.props;
        const textLen = data.str?.toString().length || 0;
        this._focusOffset = textLen;
        if (window.getSelection) {
            this._focusOffset = window.getSelection().focusOffset;
            const { offsetX, offsetY } = e.nativeEvent;

            if (this._focusOffset === null) {
                this._focusOffset = textLen;
            } else {
                if (this.cellRef.current && (this._focusOffset === 0 || this._focusOffset === textLen)) {
                    const { offsetHeight, offsetWidth } = this.cellRef.current;
                    if (
                        offsetY < SPAN_TEXT_PADDING.TOP ||
                        offsetY > offsetHeight - SPAN_TEXT_PADDING.BOTTOM ||
                        offsetX < SPAN_TEXT_PADDING.LEFT ||
                        offsetX > offsetWidth - SPAN_TEXT_PADDING.RIGHT
                    ) {
                        this._focusOffset = textLen;
                    }
                }
            }
        }
    };

    reRender() {
        this.cellOverReset();
        const renderFlag = !this.state.renderFlag;
        this.setState({
            ...this.state,
            renderFlag,
        });
    }

    render() {
        const { data, mouseEventMap, addStyle, style } = this.props;
        const { down, up, move, over, out, rightClick, click, dobuleClick } = mouseEventMap || {};

        return (
            <div
                style={style}
                ref={this.cellRef}
                key={`_${data.col}_${data.row}`}
                className={`${Styles.cell} ${
                    data.isSelect ? (data.isSelectAll ? Styles.select_all : Styles.select) : ''
                }`}
                onClick={click || dobuleClick ? this._onClick : undefined}
                onMouseDown={down ? (e) => down(data, e) : undefined}
                onMouseMove={move ? (e) => move(data, e) : undefined}
                onMouseOver={over ? (e) => over(data, e) : undefined}
                onMouseOut={out ? (e) => out(data, e) : undefined}
                onMouseUp={up ? () => up(data) : undefined}
                onContextMenu={rightClick ? (e) => rightClick(data, e) : undefined}>
                <span style={addStyle} ref={this.textRef} className={Styles.cell_inner}>
                    {data.str}
                </span>
            </div>
        );
    }
}

