/* eslint-disable react/no-deprecated */
import React from 'react';
import { Dispatch, connect } from 'react-redux';
import Styles from '@assets/scss/TableSheet.module.scss';
import { SheetModifyRect, StoreState } from '@dataTypes';
import { EditorModifyData, EDITOR_MODIFY_TYPE } from '@sheet/model/editor-modify-data';
import { sheetActions } from '@sheet/reducers/sheet-reducer';
import StoreManager from '@sheet/helper/store-manager';
import { ModifyData, MODIFY_TYPE } from '@sheet/model/modify-data';
import { editorActions } from '@sheet/reducers/editor-reducer';
import { MODIFY_RECT_TYPE } from '@sheet/model/modify-rect-type';
import config from '@sheet/config';
import { cLog } from '@debug/logger';

const { editorModifyData, modifyRect } = editorActions;
const { copyCell, deleteCell, cutCell } = sheetActions;
interface CellItemProps {
    modifyData?: EditorModifyData;
    modifyRect?: SheetModifyRect;
    onMaxCell?: () => void;

    addEditorModifyData: (eModifyData) => void;
    copyCell: (modifyCell) => void;
    deleteCell: (modifyCell) => void;
    cutCell: (modifyCell) => void;
    setModifyRect: (modifyCell) => void;
}

class CellOptionLayer extends React.Component<CellItemProps> {
    state = {};

    componentWillUnmount() {}

    componentWillMount() {}

    componentDidMount() {}

    componentDidUpdate() {
        cLog('[CellOptionLayer] componentDidUpdate');
    }

    _onContextMenu = (e) => {
        e.preventDefault();
    };

    _onCopy = (e) => {
        e.preventDefault();
        cLog('COPY');

        const { setModifyRect, addEditorModifyData } = this.props;
        setModifyRect(MODIFY_RECT_TYPE.COPY);
        addEditorModifyData(undefined);
    };

    _onCut = (e) => {
        e.preventDefault();
        cLog('CUT');

        const { setModifyRect, addEditorModifyData } = this.props;
        setModifyRect(MODIFY_RECT_TYPE.CUT);
        addEditorModifyData(undefined);
    };

    _onPaste = (e) => {
        e.preventDefault();
        cLog('PASTE');

        const { setModifyRect, addEditorModifyData, copyCell, cutCell, modifyRect } = this.props;
        if (modifyRect) {
            const { sc, ec, sr, er, type } = modifyRect;
            const { rect, selectCell } = StoreManager.state.editor;

            const start = { col: sc, row: sr };
            const end = { col: ec, row: er };
            let modify;

            if (rect) {
                const { sc: col, sr: row } = rect;
                modify = { col, row };
            } else if (selectCell) {
                modify = { col: selectCell.col, row: selectCell.row };
            }

            let col = 0;
            let row = 0;
            const modifyCol = modify.col + (ec - sc);
            const modifyRow = modify.row + (er - sr);

            const { sheetItems } = StoreManager.state.sheet;
            if (sheetItems) {
                col = sheetItems.length;
                row = col ? sheetItems[0].length : 0;
            }

            const resultColLen = Math.max(col, modifyCol + 1);
            const resultRowLen = Math.max(row, modifyRow + 1);

            if (resultColLen * resultRowLen > config.MAX_CELL) {
                const { onMaxCell } = this.props;
                if (onMaxCell) onMaxCell();
            } else if (modify) {
                const modifyData = new ModifyData();
                modifyData.start = start;
                modifyData.end = end;
                modifyData.modifyItem = modify;

                if (type === MODIFY_RECT_TYPE.COPY) {
                    modifyData.type = MODIFY_TYPE.COPY;
                    copyCell(modifyData);
                } else if (type === MODIFY_RECT_TYPE.CUT) {
                    modifyData.type = MODIFY_TYPE.CUT;
                    cutCell(modifyData);
                }

                setModifyRect(undefined);
            }

            addEditorModifyData(undefined);
        }
    };

    _onDelete = (e) => {
        e.preventDefault();
        cLog('DELETE');
        const { deleteCell, addEditorModifyData, setModifyRect } = this.props;
        let modifyData: ModifyData | null = new ModifyData();
        modifyData.type = MODIFY_TYPE.DELETE;
        const { rect, selectCell } = StoreManager.state.editor;

        if (rect) {
            const { sc, ec, sr, er } = rect;
            modifyData.start = { col: sc, row: sr };
            modifyData.end = { col: ec, row: er };
        } else if (selectCell) {
            modifyData.start = selectCell;
            modifyData.end = selectCell;
        } else {
            modifyData = null;
        }

        if (modifyData) {
            addEditorModifyData(undefined);
            deleteCell(modifyData);
        }

        setModifyRect(undefined);
    };

    render() {
        const { modifyData, modifyRect } = this.props;
        let position = {};
        let isShow;

        if (modifyData) {
            const { positionCSS, type } = modifyData;
            position = positionCSS || {};
            isShow = type === EDITOR_MODIFY_TYPE.CELL;
        }

        return modifyData && isShow ? (
            <div className={Styles.cell_layer} style={position} onContextMenu={this._onContextMenu}>
                <ul>
                    <li>
                        <a href="/" className={Styles.link} onClick={this._onCut}>
                            {config.LANGUAGE.cut}
                        </a>
                    </li>
                    <li>
                        <a href="/" className={Styles.link} onClick={this._onCopy}>
                            {config.LANGUAGE.copy}
                        </a>
                    </li>
                    {modifyRect ? (
                        <li>
                            <a href="/" className={Styles.link} onClick={this._onPaste}>
                                {config.LANGUAGE.paste}
                            </a>
                        </li>
                    ) : (
                        <></>
                    )}

                    <li>
                        <a href="/" className={Styles.link} onClick={this._onDelete}>
                            {config.LANGUAGE.delete}
                        </a>
                    </li>
                </ul>
            </div>
        ) : (
            <></>
        );
    }
}

const mapStateToProps = (state: StoreState) => ({
    modifyData: state.editor.modifyData,
    modifyRect: state.editor.modifyRect,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    addEditorModifyData: (eModifyData) => dispatch(editorModifyData(eModifyData)),

    copyCell: (modifyCell) => dispatch(copyCell(modifyCell)),
    deleteCell: (modifyCell) => dispatch(deleteCell(modifyCell)),
    cutCell: (modifyCell) => dispatch(cutCell(modifyCell)),
    setModifyRect: (modifyCell) => dispatch(modifyRect(modifyCell)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(CellOptionLayer);
