import { IBaseItem, SheetModifyRect } from '@dataTypes';
import config from '@sheet/config';
import StoreManager from '@sheet/helper/store-manager';
// import { transform, isEqual, isObject, size } from 'lodash';

const setDiffValue = (obj, v1, v2, k1, k2) => {
    if (v1 > v2) {
        obj[k1] = v2;
        obj[k2] = v1;
    } else {
        obj[k1] = v1;
        obj[k2] = v2;
    }
};

export const getRect: (item1?: IBaseItem, item2?: IBaseItem) => SheetModifyRect | null = (
    item1?: IBaseItem,
    item2?: IBaseItem,
) => {
    if (item1 === undefined || item2 === undefined) return null;

    const { col: sCol, row: sRow } = item1;
    const { col: eCol, row: eRow } = item2;

    const rect: any = {};

    setDiffValue(rect, sRow, eRow, 'sr', 'er');
    setDiffValue(rect, sCol, eCol, 'sc', 'ec');

    return rect as SheetModifyRect;
};

const TABLE_SHEET_SIZE = config.SHEET_TABLE.TABLE_SHEET_SIZE;
const { CELL_WIDTH, CELL_HEIGHT } = TABLE_SHEET_SIZE;

export const getCellPositionCSS = (cellItem?: IBaseItem, marginX = 0, marginY = 0) => {
    if (!cellItem) return {};

    const { col, row } = cellItem;
    const left = `${CELL_WIDTH * (row || 0) + marginX}px`;
    const top = `${CELL_HEIGHT * (col || 0) + marginY}px`;

    return { left, top };
};

export const setSelectionRange = (input, selectionStart, selectionEnd) => {
    if (input.setSelectionRange) {
        input.focus();
        input.setSelectionRange(selectionStart, selectionEnd);
    } else if (input.createTextRange) {
        const range = input.createTextRange();
        range.collapse(true);
        range.moveEnd('character', selectionEnd);
        range.moveStart('character', selectionStart);
        range.select();
    }
};

export const setCaretToPos = (input, pos) => {
    setSelectionRange(input, pos, pos);
};

export const getRowLength = () => {
    const { rows } = StoreManager.state.sheet.fields;
    return rows ? rows.length || 0 : 0;
};

export const getColLength = () => {
    const { cols } = StoreManager.state.sheet.fields;
    return cols ? cols.length || 0 : 0;
};

export const getSaveData = () => {
    const data: any[][] = [];
    const { sheetItems, originData } = StoreManager.state.sheet;
    let length = 0;
    if (sheetItems) {
        const colLen = sheetItems.length;
        const rowLen = sheetItems[0].length;
        for (let col = 0; col < colLen; col++) {
            data[col] = [];
            const rows = sheetItems[col];
            for (let row = 0; row < rowLen; row++) {
                data[col][row] = rows[row].str || '';
            }
        }

        length = colLen * rowLen;
    }

    const fields = originData?.fields || { cols: undefined, rows: undefined };

    return { data, length, fields };
};

export const numberWithCommas = (n) => {
    return n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
};

